<template>
  <div id="app">
      <router-view></router-view>
      <loading v-show="fetchLoading"></loading>
  </div>
</template>

<script>
  import loading from './components/public/load.vue'
export default {
  name: 'app',
  data() {
      return {

      }
  },
  computed:{
    fetchLoading(){
        return this.$store.getters.fetchLoading;
    }
  },
  components: {
      loading
  },
  mounted(){
    this.$store.dispatch("fetch_load" ,true);
  }
}
</script>

<style>
  .ivu-modal-content {
    position: relative;
    background-color: #fff;
    border: 0;
    border-radius: 6px;
    margin-top: 0.2rem;
    margin-top: 0.4rem;
    background-clip: padding-box;
}
</style>



