import Vue from 'vue'
import Router from 'vue-router'
//电商小程序
const index = () => import('@/components/index.vue')
const goods = () => import('@/components/store/content/goods.vue')
const info = () => import('@/components/store/info/info.vue')
const pay = () => import('@/components/wxPay/pay.vue')
const style = () => import('@/components/style/style.vue')
const card = () => import('@/components/store/card/card.vue')
const sorts = () => import('@/components/store/sorts/sorts.vue')
const delivery = () => import('@/components/store/delivery/delivery.vue')
const inform = () => import('@/components/inform/inform.vue')
const login = () => import('@/components/login/login.vue')
const forget = () => import('@/components/login/forget.vue')
const establish = () => import('@/components/establish/establish.vue')
const charge = () => import('@/components/establish/charge.vue')
const createApp = () => import('@/components/establish/createApp.vue')
const payment = () => import('@/components/establish/payment.vue')
const user = () => import('@/components/store/user/user.vue')
const orderList = () => import('@/components/store/order/orderList.vue')
const orderInfo = () => import('@/components/store/order/orderInfo.vue')
const template = () => import('@/components/store/template/template.vue')
const templateInfo = () => import('@/components/store/template/templateInfo.vue')
const setTemplate = () => import('@/components/store/template/setTemplate.vue')


//预约小程序
const orderInedex = () => import('@/components/orderInedex.vue')
const serve = () => import('@/components/subscribe/serve/serve.vue')
const serveSorts = () => import('@/components/subscribe/serveSorts/serveSorts.vue')
const serveInfo = () => import('@/components/subscribe/serveInfo/serveInfo.vue')
const set = () => import('@/components/subscribe/set/set.vue')
const staff = () => import('@/components/subscribe/staff/staff.vue')
const bookStyle = () => import('@/components/subscribe/style/bookStyle.vue')
const bookOrder = () => import('@/components/subscribe/order/orderList.vue')
const bookOrderInfo = () => import('@/components/subscribe/order/orderInfo.vue')
const orderUser = () => import('@/components/subscribe/orderUser/orderUser.vue')
// 酒店预订
const hotel = ()=> import ('@/components/hotel.vue')
const hotelSign = ()=> import('@/components/hotelComponents/hotelSign.vue')
const hotelStyle = ()=> import('@/components/hotelComponents/hotelStyle.vue')
const hotelRoom = () =>import('@/components/hotelComponents/hotelHome.vue')
const hotelPrice = () =>import('@/components/hotelComponents/hotelPrice.vue')
const store = () => import('@/components/hotelComponents/storeManger.vue')
const hotelList = () => import('@/components/hotelComponents/hotel/hotelList.vue')
const hotelIn = () => import('@/components/hotelComponents/hotel/hotelInfo.vue')
const hotelUser = () => import('@/components/hotelComponents/hotelUser/hotelUser.vue')
Vue.use(Router)
export default new Router({
  // mode: 'history',
  // base:'/system',
  routes: [
    {
      path:'/login',
      name: 'login',
      component: login,
    },
    {
      path:'/charge',
      name: 'charge',
      component: charge,
    },
    {
      path: '*',
      redirect:'/login'
    },
    {
      path: '/establish',
      name: 'establish',
      component: establish,
    },
    {
      path: '/forget',
      name: 'forget',
      component: forget,
    },
    {
      path: '/createApp',
      name: 'createApp',
      component: createApp,
    },
    {
      path: '/payment/:appid',
      name: 'payment',
      component: payment,
    },
//  预约

     {
      path: '/:appid/orderIndex',
      name: 'orderIndex',
      meta: {
        requireAuth: true
      },
       component: orderInedex,
      children: [{
          path: 'serve',
          name: 'serve',
          meta: {
            requireAuth: true
          },
          component: serve
      },
      {
          path: 'serveSorts',
          name: 'serveSorts',
          meta: {
            requireAuth: true
          },
          component: serveSorts
      },{
          path: 'serveInfo',
          name: 'serveInfo',
          meta: {
            requireAuth: true
          },
          component: serveInfo
      },{
          path: 'set',
          name: 'set',
          meta: {
            requireAuth: true
          },
          component: set
      },{
          path: 'staff',
          name: 'staff',
          meta: {
            requireAuth: true
          },
          component: staff
      },
       {
          path: 'style',
          meta: {
            requireAuth: true
          },
          name: 'bookStyle',
          component: bookStyle,
        },
        {
          path: 'inform',
          meta: {
            requireAuth: true
          },
          name: 'bookInform',
          component: inform,
        },
        {
          path: 'orderList',
          meta: {
            requireAuth: true
          },
          name: 'bookOrder',
          component: bookOrder,
        },
        {
          path: 'orderInfo/:id',
          meta: {
            requireAuth: true
          },
          name: 'bookOrderInfo',
          component: bookOrderInfo,
        },
        {
          path: 'orderUser',
          meta: {
            requireAuth: true
          },
          name: 'orderUser',
          component: orderUser,
        },
      ]

    },

//  电商
     {
      path: '/:appid/editor',
      name: 'index',
      meta: {
        requireAuth: true
      },
      component: index,
      children: [{
          path: 'goods',
          name: 'goods',
          meta: {
            requireAuth: true
          },
          component: goods,
        }, {
          path: 'info',
          name: 'info',
          meta: {
            requireAuth: true
          },
          component: info,
        }, {
          path: 'pay',
          meta: {
            requireAuth: true
          },

          name: 'pay',
          component: pay,
        }, {
          path: 'style',
          meta: {
            requireAuth: true
          },
          name: 'style',
          component: style,
        }, {
          path: 'card',
          meta: {
            requireAuth: true
          },
          name: 'card',
          component: card,
        }, {
          path: 'sorts',
          name: 'sorts',
          meta: {
            requireAuth: true
          },
          component: sorts,
        }, {
          path: 'delivery',
          meta: {
            requireAuth: true
          },
          name: 'delivery',
          component: delivery,
        }, {
          path: 'inform',
          meta: {
            requireAuth: true
          },
          name: 'inform',
          component: inform,
        }, {
          path: 'user',
          meta: {
            requireAuth: true
          },
          name: 'user',
          component: user,
        }, {
          path: 'orderList',
          meta: {
            requireAuth: true
          },

          name: 'orderList',
          component: orderList,
        }, {
          path: 'orderInfo/:id',
          meta: {
            requireAuth: true
          },
          name: 'orderInfo',
          component: orderInfo,
        }, {
          path: 'template',
          meta: {
            requireAuth: true
          },
          name: 'template',
          component: template,
        }, {
          path: 'setTemplate/:id',
          meta: {
            requireAuth: true
          },
          name: 'setTemplate',
          component: setTemplate,
        }]
    },
    // 酒店
    {
      path:'/:appid/hotel',
      component:hotel,
      // meta:{requireAuth:true},
      children:[{
        path:'',
        redirect:{
          name:'hotelInfo'
        }
      },{
         path:'hotelSign',
         name:'hotelInfo',
         meta: {
            requireAuth: true
          },
         component:hotelSign
      },{
        path:'inform',
        name:'hotelInform',
        meta: {
            requireAuth: true
        },
        component:inform,
      },{
        path:'pay',
        name:'hotelPay',
        meta: {
            requireAuth: true
        },
        component:pay,
      },{
        path:'hotelStyle',
        name:'hotelStyle',
        meta: {
            requireAuth: true
        },
        component:hotelStyle
      },{
        path:"hotelRoom",
        name:'hotelRoom',
        meta: {
            requireAuth: true
        },
        component:hotelRoom
      },{
        path:"hotelPrice",
        name:"hotelPrice",
        meta: {
            requireAuth: true
        },
        component:hotelPrice
      },{
        path:"store",
        name:"store",
        meta: {
            requireAuth: true
        },
        component:store
      },{
          path: 'hotelList',
          meta: {
            requireAuth: true
          },
          name: 'hotelList',
          component: hotelList,
        },
        {
          path: 'hotelIn/:id',
          meta: {
            requireAuth: true
          },
          name: 'hotelIn',
          component: hotelIn,
        },
         {
          path: 'hotelUser',
          meta: {
            requireAuth: true
          },
          name: 'hotelUser',
          component: hotelUser,
        },
      ]
    }

  ]
})



