const state = {
  fetchLoading: true
};
const getters = {
  // 请求加在状态
  fetchLoading: state => state.fetchLoading
};
const actions = {
  fetch_load({commit},res){
      commit("setFetchLoading",res);
  }
};
const mutations = {
  setFetchLoading (state,res) {
    state.fetchLoading = res;
  }
};
export default {
  state,
  getters,
  actions,
  mutations
}



