import Vue from 'vue'
import Vuex from 'vuex'
import shopping from './module/shopping'
import common from './module/common'
import hotel from './module/hotel'
Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    // 首页/充值页返回按钮
    pre:false,
    //个人信息
    info:"",
    // save phone show or hide
    status: true,
    ec: false,
    ecContent: true,
    ection: false,
    purchase: false,
    appid: '',
    publish: 0,
    photo: '',
    addShow:false,
    //  已经上传图片选择
    photoed: '',
  //  轮播图设置
    carousel:[],
  //  添加轮播标识
    carouselFlag:0,
  //  删除轮播
  //   商品图片保存标识 0 头像 1 商品图片 2商品详情 3轮播图  4酒店封面图
    goodsPhoto:0,
    // 商品图片
    imgLists:[],
    // 删除列表
  //  base基本信息
    baseInfo:{},
  //  获取本地的个人信息
  //获取商品详情图片
    goodsInfo:[],

    // 酒店 封面图片路径
    hotelImg:'',
    // 酒店默认显示图片index
    homeImgIndex:0,
// 手机端展示信息
    phoneMsg:{ext:
      {
        attr: {
          siteId:"11075285",
          logoUrl:"//nzr2ybsda.qnssl.com/images/158865/FlZkrQoe90NZu9mzCfq8F2KQTPhA.gif?imageMogr2/strip/thumbnail/!300x300r/gravity/Center/crop/300x300/quality/90!/format/gif",
          siteUrl:"www.xiguakeji.cc",
          layout:"0",
          searchEnable:true,
          name:"西瓜科技电商模板",
          phone:"15622187070",
          location:"河南省郑州市金水区",
          coordinate:{"lat":34.84928,"lng":113.74122},
          showContactBtn:true,
          description:"美妆小铺，123"},
        style:{
          mainBackground:"#00afe9"
          }
        },
        window:{
          navigationBarTitleText:"西瓜科技电商模板",
          navigationBarBackgroundColor:"#00afe9",
          navigationBarTextStyle:"white",
          backgroundColor:"#00c1e4"
        },
        tabBar:{
          selectedColor:"#00afe9",
          backgroundColor:"#222222",
          list:[
            {"iconPath":"assets/icons/iconset-home.png", "selectedIconPath":"assets/icons/iconset-home-selected_9.png", "pagePath":"pages/index/index","text":"主页"},
            {"iconPath":"assets/icons/iconset-cart.png","selectedIconPath":"assets/icons/iconset-cart-selected_9.png","pagePath":"pages/ecommerce/cart","text":"购物车"},
            {"iconPath":"assets/icons/iconset-order.png","selectedIconPath":"assets/icons/iconset-order-selected_9.png","pagePath":"pages/ecommerce/order","text":"订单"},
            {"iconPath":"assets/icons/iconset-more.png","selectedIconPath":"assets/icons/iconset-more-selected_9.png","pagePath":"pages/more/more","text":"更多"}]},
          extEnable:true,
          extAppid:"wxcc7d6937a1dc7290"
    }
  },
  getters:{
    //获取返回按钮
    getPre: state => state.pre,
    //获取个人信息
    getInfo: state => state.info,
    //获取手机端信息
    getPhoneMsg: state => state.phoneMsg,
    //获取商品详情
    getGoodsInfo: state => state.goodsInfo,
    //获取基本信息
    getBaseInfo: state => state.baseInfo,
    //获取所有图片列表
    getImgLists: state => state.imgLists,
    //获取标识
    getGoodsPhoto: state => state.goodsPhoto,
    //获取轮播图设置
    getCarousel: state => state.carousel,
    // 获取已经上传的图片
    getPhotoed: state => state.photoed,
    getStatus: state => state.status,
  //  获取appid
    getPublish: state => state.publish,
    getPhoto: state => state.photo,
  },
  mutations:{
    setPre(state,params){
      return state.pre = params;
    },
    //设置个人信息
    setInfo(state,params){
      return state.info = params;
    },
    //设置手机端信息
    setPhoneMsg(state,params){
      if("attr" in params){
        Object.assign(state.phoneMsg.ext.attr,params["attr"]);
      }
      if("tabBar" in params){
        Object.assign(state.phoneMsg.tabBar,params["tabBar"]);
      }
      if("window" in params){
        Object.assign(state.phoneMsg.window,params["window"]);
      }
    },
    //设置商品详情
    setGoodsInfo(state,params){
      return state.goodsInfo=params;
    },
    //获取本地
    setLocalBaseInfo(state){
      let base=JSON.parse(sessionStorage.getItem("baseInfo"));
      if(base!==null){
        return state.baseInfo=base;
      }else{
        return state.baseInfo={};
      }
    },
    //设置基本信息
    setBaseInfo(state,params){
      return state.baseInfo=params;
    },
    //设置商品图片
    setImgLists(state,params){
        return state.imgLists=params;
      },
    deleteImgLists(state,params){
        return state.imgLists.splice(params,1);
    },
    //设置商品标识
    setGoodsPhoto(state,params){
        return state.goodsPhoto=params;
    },
    setCarouselFlag(state,params){
      return state.carouselFlag=params;
    },
    setCarousel(state,params){
      if(state.carouselFlag.flag===1){
        return state.carousel.push(params);
      }else if(state.carouselFlag.flag===2){
          return state.carousel.splice(state.carouselFlag.id,1,params);
      }
    },
    //删除轮播
    deleteCarousel(state,params){
        return state.carousel.splice(params,1);
    },
    setPhotoed(state,params){
      return state.photoed=params;
    },
    setStatus(state,params){
      return state.status=params;
    },
    setPublish(state,params){
      return state.publish=params;
    },
    setPhoto(state,params){
      return state.photo=params;
    },
    // 设置 酒店封面照片
    setHotelHomeImg(state,params){
      return state.hotelImg = params.imgUrl,state.homeImgIndex = params.imgIndex
    },
    // 设置默认 图片index
    setHomeImgIndex(state,params){
      return state.homeImgIndex = params
    }
  },
  actions:{
    pre({commit},params){
      commit("setPre",params);
    },
    info({commit},params){
      commit('setInfo',params);
    },
    //设置手机端的信息
    phoneMsg({commit},params){
      commit("setPhoneMsg",params)
    },
    //
    goodsInfo({commit},params){
      commit("setGoodsInfo",params);
    },
    //获取本地信息
    localBaseInfo({commit}){
      commit("setLocalBaseInfo");
    },
    //
    baseInfo({commit},params){
      commit("setBaseInfo",params)
    },
    // 设置图片标识
    imgLists({commit},params){
        commit('setImgLists',params);
    },
    //设置标识
    goodsPhoto({commit},params){
        commit("setGoodsPhoto",params);
    },
    carousel({commit},params){
      commit('setCarousel',params);
    },
    photoed({commit},params){
        commit('setPhotoed',params);
    },
    // 设置 酒店封面照片 action
    hotelHomeImg({commit},params){
      commit('setHotelHomeImg',params)
    },
    // 创建app
    publish({commit},params){
      commit('setPublish',params);
    },
    Photo({commit},params){
      commit('setPhoto',params);
    },
  },
  modules:{
    shop: shopping,
    b: common,
    hotel:hotel
  }
})



